.active-modal-container {
  display: block;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.8); /* Black w/ opacity */
  display: grid;
  place-items: center;
  overflow: hidden;
  .filter-modal {
    box-sizing: border-box;
    width: 512px;
    height: 471px;
    background: #ffffff 0% 0% no-repeat padding-box;
    border-radius: 15px;
    opacity: 1;
    display: grid;
    place-items: center;
    padding: 37px 40px 59px;
    position: relative;
    .filter-modal-cancel-button {
      margin: 0;
      position: absolute;
      top: 27px;
      right: 27px;
      width: 14px;
      height: 14px;
      opacity: 1;
      font: normal normal normal 24px/18px "poppins";
      cursor: pointer;
    }
    .modal-content-container {
      box-sizing: border-box;
      height: 100%;
      width: 100%;
      position: relative;
      .filter-header {
        width: 50px;
        height: 24px;
        text-align: center;
        font-family: "poppins";
        font-size: 17px;
        font-weight: normal;
        line-height: 26px;
        letter-spacing: -0.01px;
        color: #222222;
        text-transform: uppercase;
        opacity: 1;
        margin: 0 0 25px 0;
      }
      .filters-container {
        box-sizing: border-box;
        width: 100%;
        height: 73px;
        background: #ffffff 0% 0% no-repeat padding-box;
        border: 1px solid #cccccc;
        border-radius: 5px;
        opacity: 1;
        margin: 15px 0 0 0;
        cursor: pointer;
        .filter-sub-header {
          width: 70px;
          height: 21px;
          text-align: center;
          font: normal normal normal 15px/23px Poppins;
          letter-spacing: 0px;
          color: #583ae0;
          opacity: 1;
        }
        .filter-content {
          width: 96px;
          height: 17px;
          text-align: center;
          font: normal normal normal 12px/18px Poppins;
          letter-spacing: 0px;
          color: #222222;
          opacity: 1;
          margin: 8px 0 0 15px;
        }
      }
      .filter-modal-footer-button {
        position: absolute;
        bottom: 0;
        width: 100%;
        display: flex;
        justify-content: space-between;
        .filter-button {
          width: 179px;
          height: 35px;
          border: 1px solid #583ae0;
          border-radius: 6px;
          opacity: 1;
          outline: none;
          cursor: pointer;
        }
        .clear-button {
          background: #ffffff 0% 0% no-repeat padding-box;
          font: normal normal medium 15px/25px Poppins;
          letter-spacing: 0px;
          color: #583ae0;
          text-transform: uppercase;
          opacity: 1;
        }
        .apply-button {
          background: #583ae0 0% 0% no-repeat padding-box;
          font: normal normal medium 15px/25px Poppins;
          letter-spacing: 0px;
          color: #ffffff;
          text-transform: uppercase;
          opacity: 1;
        }
      }
    }
  }
}
.inactive-modal-container {
  display: none;
}
